.footer-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 20px;
  margin-top: auto;
  color: #080808;
  text-align: center;
  font-family: Roboto;
  font-size: 14px;
  cursor: pointer;
}

footer {
  background-color: #f7f7f7;
  width: 100%;
}

.text-direction {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-left: 10px;
}

.heart {
  color: #b92f2c;
  width: 50px; /* Simplificat */
  height: 25.76px; /* Simplificat */
  flex-shrink: 0;
  animation: pulse 1.5s infinite ease-in-out;
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

.students {
  color: #b92f2c;
  font-weight: bold;
}

.footer-modal {
  display: none;
  position: fixed;
  z-index: 1000; /* Îmbunătățit */
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.8);
}

.footer-modal-content {
  border-radius: 10px;
  background-color: #f7f7f7;
  padding: 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-height: 80vh;
  max-width: 600px;
  background: rgba(32, 32, 32, 0.7);
  box-shadow: 0 10px 30px #b92f2c;
  border: 2px outset #b92f2c;
  color: white;
  overflow: hidden;
}
.title-btn-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  margin-bottom: 15px;
}
.footer-modal-title {
  font-size: 18px;
}
.close-button-member {
  background-color: transparent;
  border: none;
}

.close-button-member svg {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.close-button-member:hover,
.close-button-member:focus {
  scale: 1.2;
}
.group-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.member-section {
  background-color: rgb(185, 47, 44, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  border-radius: 10px;
  border: 1px solid #fff;
  padding: 10px;
}

.member-title,
.member-title1 {
  color: white;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
}

.last-image {
  width: 60px;
  border-radius: 50%;
  border: 2px solid #f0eae6;
}
.member-section:hover .last-image {
  border: 3px solid #b92f2c;
}
.member-section:hover {
  background-color: rgb(255, 255, 255, 0.9);
  color: #b92f2c;
}
.member-section:hover .member-description,
.member-section:hover .member-name {
  color: #b92f2c;
}
.member-links svg {
  width: 30px;
  height: 30px;
  transition: fill 0.5s;
}

.member-links .social-links {
  cursor: pointer;
}

.member-links svg:hover {
  fill: #b92f2c;
}
.member-section {
  /* stiluri existente */
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.member-section:hover {
  box-shadow: 0 0 15px 10px rgba(185, 47, 44, 0.5); /* culoarea și dimensiunea umbrei */
  transform: scale(1.05); /* ușoară mărire pentru a sublinia efectul */
}

.footer-modal-title:hover:after {
  left: 100%;
  transform: skewX(30deg) skewY(30deg);
}

.footer-modal-title:before,
.footer-modal-title:after {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 200%;
  background: rgba(255, 255, 255, 0.20);
  transition: 0.9s;
  pointer-events: none;
}

.footer-modal-title:before {
  left: -100%;
}

.footer-modal-title:after {
  right: -100%;
}


// media q -------------------------

@media (max-width: 500px) {
  .footer-modal-content {
    width: 90%;
  }
  .member-name {
    font-size: 14px;
  }
  .member-description {
    font-size: 12px;
  }
}

